import React from 'react';
import { Link } from 'react-router-dom';

const ReturnsPolicy = () => {

    return (
        <div className="main__returns returns-main">
            <div className="returns-main__container">
                <div className="returns-main__content">
                    <h2 className="returns-main__title">
                        Poltica de Devoluo
                    </h2>
                    <div className="returns-main__text">
                        <p>
                            Bem-vindo  nossa poltica de devoluo.
                            Agradecemos a sua confiana em adquirir os nossos produtos.
                            Em conformidade com a legislao portuguesa e para garantir
                            a sua satisfao, apresentamos a nossa poltica de
                            devoluo detalhada.                            
                        </p>
                    </div>
                    <ul className="returns-main__items">
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                1. Condies de Devoluo
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    As devolues de produtos adquiridos no nosso website
                                    so permitidas apenas nas seguintes condies:
                                </p>
                                <p>
                                    <b>Defeito de Fabricao:</b> Aceitamos devolues exclusivamente
                                    para produtos que apresentem defeitos de fabrico.
                                    No sero aceitas devolues por outros motivos, tais
                                    como mudana de opinio, erro na escolha do produto,
                                    ou uso inadequado.
                                </p>                                
                            </div>
                        </li>
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                2. Procedimento de Devoluo
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    Se o seu produto apresentar um defeito de fabrico, siga os
                                    passos abaixo para efetuar a devoluo:
                                </p>
                                <p>
                                    <b>Contacte o Suporte ao Cliente:</b> Envie um e-mail
                                    para <Link to="mailto:bestbuybeauty.pt@gmail.com" className="returns-main__link">
                                        bestbuybeauty.pt@gmail.com
                                    </Link> com a descrio detalhada do
                                    defeito, nmero do pedido, e anexando fotografias que
                                    comprovem o defeito.
                                </p>   
                                <p>
                                    <b>Anlise da Solicitao:</b> Aps o envio do e-mail, a nossa
                                    equipa de suporte analisar a sua solicitao e, se necessrio,
                                    poder pedir informaes adicionais. Este processo pode demorar
                                    at <b>5 dias</b> teis.
                                </p>
                                <p>
                                    <b>Autorizao de Devoluo:</b> Se a devoluo for aprovada,
                                    enviaremos um e-mail com a autorizao de devoluo e instrues
                                    para envio do produto defeituoso de volta s nossas instalaes.
                                </p>
                                <p>
                                    <b>Envio do Produto:</b> Embale o produto de forma segura e envie-o para
                                    o endereo indicado no e-mail de autorizao. Os custos de envio
                                    sero reembolsados se o defeito de fabrico for confirmado.
                                </p>
                            </div>
                        </li>
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                3. Condies do Produto Devolvido
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    Para que a devoluo seja aceite, o produto deve ser
                                    devolvido nas seguintes condies:
                                </p>
                                <p>
                                    <b>Estado Original:</b> O produto deve estar em seu estado
                                    original, sem sinais de uso indevido, e acompanhado de
                                    todos os acessrios e embalagens originais.
                                </p> 
                                <p>
                                    <b>Comprovante de Compra:</b>  necessrio incluir o comprovante
                                    de compra ou a fatura original.
                                </p>
                            </div>
                        </li>
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                4. Avaliao e Resoluo
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    Aps a receo do produto devolvido, ser realizada
                                    uma avaliao tcnica para confirmar o defeito de fabrico:
                                </p>
                                <p>
                                    <b>Confirmao do Defeito:</b> Se o defeito de fabrico for confirmado,
                                    procederemos  substituio do produto por outro igual ou,
                                    se preferir, ao reembolso total do valor pago, incluindo os
                                    custos de envio.
                                </p> 
                                <p>
                                    <b>Defeito No Confirmado:</b> Se no for constatado qualquer defeito
                                    de fabrico, o produto ser devolvido ao cliente, e os custos
                                    de envio no sero reembolsados.
                                </p>
                            </div>
                        </li>
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                5. Prazo para Devoluo
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    As devolues devem ser solicitadas num prazo mximo <b>de 14 dias </b>
                                    a partir da data de receo do produto. Solicitaes fora deste
                                    prazo no sero aceites.
                                </p>
                            </div>
                        </li>
                        <li className="returns-main__item item-returns">
                            <h3 className="item-returns__title">
                                6. Contactos
                            </h3>
                            <div className="item-returns__text">
                                <p>
                                    Para qualquer questo relacionada com devolues, por favor,
                                    contacte-nos atravs de:
                                </p>
                                <p>
                                    <b>E-mail:</b> <Link to="mailto:bestbuybeauty.pt@gmail.com" className="item-returns__link">
                                        bestbuybeauty.pt@gmail.com
                                    </Link><br/>
                                    <b>Telefone:</b> <Link to="tel:+351960201624" className="item-returns__link">
                                        960201624
                                    </Link><br />
                                    <b>Endereo:</b> <Link to="https://maps.app.goo.gl/qVWYQSMTuwJ1pZaE7?g_st=com.google.maps.preview.copy" className="item-returns__link">
                                        Rua Quinta dos Arcos,
                                        Lote 22, Loja A, Armao de Pra, 8365-186, Portugal
                                    </Link>
                                </p> 
                            </div>
                        </li>
                    </ul>
                    <div className="returns-main__text">
                        <p>
                            Agradecemos pela sua compreenso e cooperao. Estamos
                            comprometidos em fornecer produtos de alta qualidade e
                            em resolver qualquer problema que possa surgir com
                            eficincia e justia.
                        </p>
                        <p>
                            <b>Nota:</b> Esta poltica de devoluo foi elaborada de acordo
                            com a legislao portuguesa e destina-se a fornecer clareza
                            e transparncia aos nossos clientes.
                        </p>                    
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ReturnsPolicy;