import React from 'react';
import { Link } from 'react-router-dom';

const PrivacyPolicy = () => {

    return (
        <div className="main__privacy privacy-main">
            <div className="privacy-main__container">
                <div className="privacy-main__content">
                    <h2 className="privacy-main__title">
                        Poltica de Privacidade da Best Buy Beauty
                    </h2>
                    <div className="privacy-main__text">
                        <p>
                            Bem-vindo  Best Buy Beauty. Esta Poltica de
                            Privacidade descreve como coletamos, usamos,
                            divulgamos e protegemos as suas informaes pessoais
                            ao visitar o nosso site <Link to="https://best-buy-beauty.com/" className="privacy-main__link">
                                best-buy-beauty.com
                            </Link>.
                        </p>
                    </div>
                    <ul className="privacy-main__items">
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                1. Informaes que Coletamos
                            </h3>
                            <h4 className="item-privacy__subtitle">
                                1.1 Informaes Pessoais
                            </h4>  
                            <div className="item-privacy__text">
                                <p>
                                    Coletamos informaes pessoais que voc nos fornece
                                    diretamente, tais como:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    Nome
                                </li>
                                <li className="item-privacy__point">
                                    Endereo de e-mail
                                </li>
                                <li className="item-privacy__point">
                                    Endereo de entrega
                                </li>
                                <li className="item-privacy__point">
                                    Nmero de telefone
                                </li>
                                <li className="item-privacy__point">
                                    Informaes de pagamento (ex. dados do carto de crdito)
                                </li>
                            </ul>
                            <h4 className="item-privacy__subtitle">
                                1.2 Informaes de Uso do Site
                            </h4>
                            <div className="item-privacy__text">
                                <p>
                                    Coletamos automaticamente informaes sobre a sua interao
                                    com o nosso site, incluindo:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    Endereo IP
                                </li>
                                <li className="item-privacy__point">
                                    Tipo de navegador
                                </li>
                                <li className="item-privacy__point">
                                    Pginas visitadas
                                </li>
                                <li className="item-privacy__point">
                                    Tempo gasto em cada pgina
                                </li>
                                <li className="item-privacy__point">
                                    Referenciadores de URL
                                </li>
                            </ul>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                2. Como Usamos as Suas Informaes
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Usamos as suas informaes pessoais para:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    Processar e gerenciar suas compras
                                </li>
                                <li className="item-privacy__point">
                                    Enviar confirmaes e atualizaes de pedidos
                                </li>
                                <li className="item-privacy__point">
                                    Responder s suas perguntas e fornecer suporte ao cliente
                                </li>
                                <li className="item-privacy__point">
                                    Enviar newsletters e outras comunicaes de marketing (com seu consentimento)
                                </li>
                                <li className="item-privacy__point">
                                    Melhorar e personalizar a sua experincia no nosso site
                                </li>
                                <li className="item-privacy__point">
                                    Cumprir com obrigaes legais e regulamentares
                                </li>
                            </ul>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                3. Compartilhamento das Suas Informaes
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Podemos compartilhar suas informaes com:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    Provedores de servios que nos ajudam a
                                    operar o nosso site e cumprir pedidos
                                </li>
                                <li className="item-privacy__point">
                                    Autoridades legais e regulatrias quando exigido por lei
                                </li>
                                <li className="item-privacy__point">
                                    Terceiros no contexto de uma fuso, aquisio ou venda de ativos
                                </li>
                            </ul>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                4. Segurana das Informaes
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Adotamos medidas de segurana adequadas para proteger suas
                                    informaes pessoais contra acesso no autorizado, alterao,
                                    divulgao ou destruio. No entanto, nenhum sistema de
                                    segurana  infalvel, e no podemos garantir a segurana
                                    absoluta das suas informaes.
                                </p>                             
                            </div>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                5. Seus Direitos
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Voc tem o direito de:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    Acessar e corrigir suas informaes pessoais
                                </li>
                                <li className="item-privacy__point">
                                    Solicitar a excluso de suas informaes pessoais
                                </li>
                                <li className="item-privacy__point">
                                    Optar por no receber comunicaes de marketing
                                </li>
                                <li className="item-privacy__point">
                                    Solicitar a portabilidade dos dados
                                </li>
                                <li className="item-privacy__point">
                                    Retirar seu consentimento a qualquer momento
                                </li>
                            </ul>
                            <div className="item-privacy__text">
                                <p>
                                    Para exercer esses direitos, entre em contato conosco
                                    atravs do e-mail <Link to="mailto:bestbuybeauty.pt@gmail.com" className="item-privacy__link">
                                        bestbuybeauty.pt@gmail.com
                                    </Link>.
                                </p>                             
                            </div>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                6. Cookies
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Usamos cookies e tecnologias semelhantes para coletar
                                    informaes sobre a sua navegao no nosso site e
                                    melhorar a sua experincia. Voc pode configurar seu
                                    navegador para recusar cookies, mas isso pode afetar
                                    a funcionalidade do site.
                                </p>                             
                            </div>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                7. Alteraes a Esta Poltica de Privacidade
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Podemos atualizar esta Poltica de Privacidade periodicamente.
                                    Publicaremos quaisquer alteraes nesta pgina e, se as
                                    alteraes forem significativas, forneceremos um aviso mais
                                    destacado.
                                </p>                             
                            </div>
                        </li>
                        <li className="privacy-main__item item-privacy">
                            <h3 className="item-privacy__title">
                                8. Contatos
                            </h3>
                            <div className="item-privacy__text">
                                <p>
                                    Se tiver dvidas sobre esta Poltica de Privacidade ou
                                    sobre como tratamos suas informaes pessoais, entre em
                                    contato conosco atravs de:
                                </p>                             
                            </div>
                            <ul className="item-privacy__list">
                                <li className="item-privacy__point">
                                    <b>E-mail:</b> <Link to="mailto:bestbuybeauty.pt@gmail.com" className="item-privacy__link">
                                        bestbuybeauty.pt@gmail.com
                                    </Link>
                                </li>
                                <li className="item-privacy__point">
                                    <b>Endereo:</b> <Link
                                        to="https://maps.app.goo.gl/qVWYQSMTuwJ1pZaE7?g_st=com.google.maps.preview.copy"
                                        className="item-privacy__link">
                                        Rua Quinta dos Arcos,
                                        Lote 22, Loja A, Armao de Pra, 8365-186, Portugal
                                    </Link>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default PrivacyPolicy;